import Layout from '../src/components/layout'
import Link from 'next/link'

const PostLink = props => (
  <li>
    <Link href={`/post?title=${props.title}`}>
      <a>{props.title}</a>
    </Link>
  </li>
)

const Index = () => (
  <Layout>
    <p>Hello Next.js</p>
    <ul>
      <PostLink title='maint1' />
      <PostLink title='maint2' />
    </ul>
  </Layout>
)

export default Index
